import React, { useState } from 'react'
import tick from './green_tick.png'
import './App.css';
import Select from 'react-select'
import {
  Flex,
  Box
} from 'rebass'
function App() {
  const [selectedValue, setSelectedValue] = useState('');
  var val = [{

    sortCode: "11-22-33",

    accountNumber: "123456789",

    accountType: "saving"
    ,
    accountName: "Mr. k"
  }, {

    sortCode: "11-22-44",

    accountNumber: "1234567",

    accountType: "current"
    ,
    accountName: "Mr. s"
  }]

  var options = []
  for (let index = 0; index < val.length; index++) {

    options.push({
      value: val[index],
      label: val[index].sortCode + " " +
        val[index].accountNumber+" "+val[index].accountType + " "
          + val[index].accountName
    })
  }
  const colorStyles = {
    control: styles => ({ ...styles, padding:8,fontSize:10 }),
    option: (styles, { data, isDisabled, isFocused, isSelected }) => {
      return {
        ...styles,
        ':before': isSelected? { 
          backgroundImage: `url(${tick})`,
          content: '"."',
          display: 'block',
          backgroundSize: '20px 20px',
          backgroundRepeat: 'no-repeat'
        } : ''
        // ':before':isSelected? {
        //   backgroundColor: 'pink',
        //   borderRadius: 10,
        //   content: '" "',
        //   display: 'block',
        //   marginRight: 8,
        //   height: 10,
        //   width: 10,
        // }:''
      }
    }
  }
  return (
    <div className="App">
      <Flex>

        <Box p={4}
          fontSize={3}
          width={[1, 1, 1 / 2]}
          >
          <h1 style={{display:'block'}}>Welcome to the Bank</h1>
          <Select
          closeMenuOnSelect={false}
            options={options}
            onBlur={(val) => { setSelectedValue(val) }}
            styles={colorStyles}
          />
        </Box>
      </Flex>
    </div>
  );
}

export default App;
